@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Yusei+Magic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Montserrat:ital,wght@0,100;0,200;0,300;1,100;1,200&family=Roboto+Condensed:ital,wght@0,400;1,300&family=Yusei+Magic&display=swap);
body {
    background-image: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%);
}

.authFormContainer {
    max-width: 320px
}

.authTitle {
    color: #19b5fe;
    font-family: 'Yusei Magic', sans-serif;
    font-size: 30px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 25%;
}

.authContainer {
    width: 100vw;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.authBtns {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 320px;
}

.authBtn {
    cursor: pointer;
    border-radius: 50%;
    border: none;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
    width: 40px;
    height: 40px;
    color: #3498db;
    background: white;
    cursor: pointer;
}

.authInput {
    border: none;
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
}

.authSubmit {
    text-align: center;
    background: #04aaff;
    color: white;
    margin-top: 10;
    cursor: pointer;
}

.authError {
    position: relative;
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}

.authSwitch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
}
.memoryCard {
    width: 500px;
    margin-left: 50%;
    transform: translate(-50%);
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
    background-color: rgba(255, 255, 255, .15);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    margin-top: 4rem;
    align-items: center;
    align-content: center;
    padding: 10px;
    padding-top: 30px;
}

.memoryImg {
    width: 40%;
    height: auto;
}

.components {
    padding: 20px;
}

.components h4 {
    color: #2574a9;
    font-size: 20px;
    padding-bottom: 20px;
}

.components p {
    font-size: 15px;
    padding-bottom: 10px;
}

.componentDate {
    position: absolute;
    top: .4rem;
    right: .4rem;
    color: #e87e04;
}

.components h3 {
    position: absolute;
    right: 2rem;
    bottom: 2rem
}

.likedPic {
    position: absolute;
    bottom: 5.5rem;
    right: 1rem;
    width: 4rem;
    height: 2rem;
    color: #f3f1ef;
}

.liked {
    color: #f4d03f;
}
html {
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
    border: 0;
}

body {
    display: flex;
    /*justify-content: center;
    align-items: center;*/
    min-height: 100vh;
    z-index: 0;
}

.loading {
    position: absolute;
    width: 100vw;
    height: 100vh;
}

.loadingText {
    font-family: 'Lobster', cursive;
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    font-size: 50px;
}

.loadingSpinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
}

.plus {
    position: fixed;
    bottom: 0;
    background-color: #81cfe0;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60px;
    height: 60px;
    font-size: 50px;
    border-radius: 50%;
}

.plus:hover {
    width: 66px;
    height: 66px;
    bottom: -6px;
}

.plusText {
    color: #e4f1fe;
}

.searchBar {
    border-radius: 20px;
    position: absolute;
    right: 4rem;
    top: 2.2rem;
    max-width: 12rem;
    height: 2rem;
    padding: 10px;
}

.container {
    transform: translate(-50%);
    margin-left: 50%;
    margin-top: 5rem;
    align-items: center;
    align-content: center;
    text-align: center;
    max-width: 500px;
}

.main {
    width: 100vw;
    margin-bottom: 10rem;
}

@media (max-width: 580px) {
    body {}
}
.profileForm {
    margin-left: 50%;
    transform: translate(-50%);
    border-bottom: 1px solid rgba(255, 255, 255, 0.9);
    padding-bottom: 30px;
    max-width: 320px;
    display: flex;
    padding: 20px;
    flex-direction: column;
}

.profileForm input {
    padding: 15px;
    border-radius: 20px;
}

.logOut {
    margin-top: 50px;
    margin-left: 50%;
    transform: translate(-50%);
    padding: 14px;
    color: whitesmoke;
    border-radius: 20px;
    background-color: #f2784b;
    margin-bottom: 10rem;
}

.profileContainer {
    position: absolute;
    margin-top: 7rem;
    width: 100vw;
    height: auto;
    background-color: #e8e8e8;
}

.PhotoBtn {
    margin-top: 20px;
    margin-left: 50%;
    transform: translate(-50%);
    padding: 17px;
}

.likedimg img {
    margin: 0;
    width: 250px;
    height: auto;
}

.likedimg {
    transform: translate(-50%);
    margin-top: 2rem;
    margin-left: 50%;
    -webkit-column-count: 2;
            column-count: 2;
    padding: 0;
    align-items: center;
}

.hideButton {
    color: #2574a9;
    top: -25px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
}

@media (max-width: 700px) {
    .likedimg {
        -webkit-column-count: 1;
                column-count: 1;
    }
}
.navTitle {
    position: absolute;
    font-size: 50px;
    left: 50%;
    top: 3rem;
    transform: translate(-50%, -50%);
    color: #5c97bf;
    font-family: 'Roboto Condensed', sans-serif;
}

.navProfile {
    position: absolute;
    right: 1rem;
    top: 2.2rem;
    color: #34495e;
}

.isMobile {
    display: none;
    color: #34495e;
    background-color: #f2f1ef;
    font-size: 1.6rem;
    top: 1.4rem;
    left: 1rem;
    position: absolute;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
}

@media (max-width: 800px) {
    .mobile {
        display: none;
    }
    .searchBar {
        border-radius: 20px;
        position: absolute;
        left: 4rem;
        top: 1.3rem;
        width: 9rem;
        height: 3.5rem;
        padding: 10px;
    }
    .isMobile {
        display: block;
    }
}
.form {
    width: 100%;
}

.memoryFormDiv {
    margin-top: 7rem;
    position: absolute;
    max-width: 300px;
    left: 50%;
    transform: translate(-50%);
    align-items: center;
}

.memoryTitleInput {
    padding: 20px;
    left: 50%;
    width: 300px;
    height: 40px;
    font-size: 20px;
    border-radius: 10px;
}

.memoryDefInput {
    padding: 20px;
    margin-top: 1rem;
    width: 300px;
    height: 100px;
    font-size: 15px;
    border-radius: 20px;
    resize: none;
}

.saveMemoryBtn {
    position: fixed;
    bottom: 2rem;
    left: 50%;
    transform: translate(-50%);
    width: 6rem;
    height: 3rem;
    font-size: 1rem;
    background-image: linear-gradient(to top, #4481eb 0%, #04befe 100%);
    border-radius: 20px;
    color: white;
}

.factoryInput__arrow {
    position: absolute;
    right: 0;
    background-color: #04aaff;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
}

.factoryInput__label {
    color: #04aaff;
    cursor: pointer;
    position: absolute;
    margin-top: 20rem;
    position: absolute;
    max-width: 300px;
    left: 50%;
    transform: translate(-50%);
}

.factoryInput__label span {
    margin-top: 1.4rem;
    margin-right: 10px;
    font-size: 12px;
}

.factoryForm__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    margin-top: 23rem;
    max-width: 300px;
    left: 50%;
    transform: translate(-50%);
    margin-bottom: 10rem;
}

.factoryForm__attachment img {
    height: 200px;
    width: auto;
}

.factoryForm__clear {
    color: #04aaff;
    cursor: pointer;
    text-align: center;
}

.factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
}


